.bargraph_container{
display: flex;
flex-direction: row;
align-items: center;
gap: 17px;
position: absolute;
width: 95vw;
height: 86px;
background-color: #FFFFFF;
border-radius: 10px;
margin-top: -20px;
margin-left: 25px;
}
.bargraph_details{
    margin-left: 21.5px;
}
.bargraph_sales_value{

font-family: Poppins;
font-size: 22px;
font-weight: 600;
line-height: 36px;
letter-spacing: 0em;
text-align: left;
color: #383874;
margin-top: 0;
margin-bottom: 14px;
}
.bargraph_sales_title{
font-family: 'Roboto';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 20px;
letter-spacing: 0.44px;
color: #383874;
margin-bottom: 0px;
margin-top: 14px;
}

.underline{
    display: flex;
    flex-direction: row;
}

.yellow_underline{
display: flex;
position: absolute;
background-color: #FFA63F;
border-radius: 100px;
width: 240.3px;
height: 6px;
left: 170px;
bottom:17px ;
top:63px ;
}
.pink_underline{
    display: flex;
    position: absolute;
    background: #FF708B;
border-radius: 100px;
width: 423.94px;
height: 6px;
/* height: 6px; */
left: 408px;
bottom:17px ;
top:63px ;
}
.purple_underline{
    display: flex;
    position: absolute;
    background-color:rgba(134, 118, 255, 1) ;
    width: 630px;
    height: 6px;
    bottom:17px ;
    top:63px ;
    right: 89px;
    left: 630px;
    border-radius: 100px;
}


/* ===================== Media Quries================== */
@media screen and (max-width:776px){
.bargraph_container{
    display: none;
}
}