* {
    scroll-behavior: smooth;
}

.map img {
    width: 60vw;
}

.tooltips_map {
    position: absolute;
    background: white;
    width: 12vw;
    height: 9vh;
    border-radius: 1vw;
}

.icon_container {
    justify-content: center;
    margin: 1.1vh 2vw 1vh 0.6vw;
    width: 3.3vw;
    height: 7vh;
    border-radius: 1.4vh;
    background-color: rgba(102, 200, 255, 1);
}

.tooltipicon {
    position: relative;
    color: #FFFFFF;
    font-size: 3vh;
    margin-left: 0.5vw;
    margin-top: 1.5vh;
    height: 4vh;
}

.tooltip_city {
    font-size: 2.4vh;
    font-weight: 300;
    font-family: 'Poppins';
    line-height: 3vh;
    text-align: left;
    margin: 2vh 0 0 0;
    color: #383874;
}

.tooltip_numbers {
    font-weight: 600;
    font-family: 'Poppins';
    font-size: 1.1vw;
    line-height: 4vh;
    align-items: center;
    color: #383874;
}

.tooltip_triangle {
    position: absolute;
    border-left: 1.5vw solid transparent;
    border-right: 1.5vw solid transparent;
    border-bottom: 1.5vw solid white;
    transform: rotate(180deg);
    left: 5vw;
    bottom: -3vh;
}

.tooltip_city_details{
    margin-left: 4.5vw;
    margin-top: -8.5vh;
}

